:root{
  --clr-purple-dark: rgb(95, 0, 186);
  --clr-purple-light: rgb(170, 125, 206);
  --clr-orange-accent-dark: #F6A81C;
  --clr-orange-accent-light: #F7D22B;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'bluu';
  src: url('../media/fonts/bluunext-bold-webfont.woff2') format('woff2'),
       url('../media/fonts/bluunext-bold-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}

@font-face {
  font-family: 'bluu';
  src: url('../media/fonts/bluunext-bolditalic-webfont.woff2') format('woff2'),
       url('../media/fonts/bluunext-bolditalic-webfont.woff') format('woff');
  font-weight: normal;
  font-style: italic;

}

@font-face {
  font-family: 'bluu_nexttitling';
  src: url('../media/fonts/bluunext-titling.woff2') format('woff2'),
       url('../media/fonts/bluunext-titling.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'SuisseRegular';
  src: url('../media/fonts/SuisseIntlMono-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'SuisseThin';
  src: url('../media/fonts/SuisseIntlMono-Thin.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'SuisseBold';
  src: url('../media/fonts/SuisseIntlMono-Bold.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Fengardoneue-Regular';
  src: url('../media/fonts/fengardoneue_regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Fengardoneue-Black';
  src: url('../media/fonts/fengardoneue_black-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Heavitas';
  src: url('../media/fonts/Heavitas.ttf');
}